'use client'

import { useCallback, useEffect, useState } from 'react'

import Alert from '@mui/material/Alert'
import Box from '@mui/material/Box'
import Button from '@mui/material/Button'
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent'
import Grid from '@mui/material/Grid'
import Stack from '@mui/material/Stack'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import TextField from '@mui/material/TextField'
import Typography from '@mui/material/Typography'

import { obtenerAnalyticsDashboard } from '@/api/analytics'

const formatNumber = value => new Intl.NumberFormat('es-DO').format(Number(value || 0))

const formatMoney = value =>
  new Intl.NumberFormat('es-DO', {
    style: 'currency',
    currency: 'DOP',
    maximumFractionDigits: 2
  }).format(Number(value || 0))

const KpiCard = ({ label, value }) => (
  <Grid item xs={12} md={6} lg={3}>
    <Card variant='outlined'>
      <CardContent>
        <Typography variant='caption' color='text.secondary'>
          {label}
        </Typography>
        <Typography variant='h5'>{value}</Typography>
      </CardContent>
    </Card>
  </Grid>
)

export default function AnalyticsModule() {
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const [analytics, setAnalytics] = useState(null)
  const [filtros, setFiltros] = useState({ fecha_desde: '', fecha_hasta: '' })

  const fetchAnalytics = useCallback(
    async customFiltros => {
      setLoading(true)
      setError('')

      try {
        const response = await obtenerAnalyticsDashboard(customFiltros || filtros)

        setAnalytics(response)
      } catch (err) {
        setError(err.message || 'No se pudo cargar analytics.')
      } finally {
        setLoading(false)
      }
    },
    [filtros]
  )

  useEffect(() => {
    fetchAnalytics({})
  }, [fetchAnalytics])

  const kpis = analytics?.kpis || {}
  const topClientes = analytics?.topClientes || []
  const series = analytics?.series?.prestamosPorMes || []

  const cuotasResumen = {
    pagadas: kpis?.cuotas?.pagadas || 0,
    pendientes: kpis?.cuotas?.pendientes || 0,
    parciales: kpis?.cuotas?.parciales || 0
  }

  return (
    <Stack spacing={2}>
      <Box>
        <Typography variant='h4'>Analytics</Typography>
        <Typography color='text.secondary'>Resumen ejecutivo del negocio.</Typography>
      </Box>

      <Stack direction={{ xs: 'column', md: 'row' }} spacing={1.5} alignItems={{ md: 'end' }}>
        <TextField
          type='date'
          label='Desde'
          value={filtros.fecha_desde}
          onChange={event => setFiltros(previous => ({ ...previous, fecha_desde: event.target.value }))}
          size='small'
          InputLabelProps={{ shrink: true }}
        />
        <TextField
          type='date'
          label='Hasta'
          value={filtros.fecha_hasta}
          onChange={event => setFiltros(previous => ({ ...previous, fecha_hasta: event.target.value }))}
          size='small'
          InputLabelProps={{ shrink: true }}
        />
        <Button variant='contained' onClick={() => fetchAnalytics()} disabled={loading}>
          Filtrar
        </Button>
        <Button
          variant='text'
          onClick={() => {
            const cleared = { fecha_desde: '', fecha_hasta: '' }

            setFiltros(cleared)
            fetchAnalytics(cleared)
          }}
          disabled={loading}
        >
          Limpiar
        </Button>
      </Stack>

      {error ? <Alert severity='error'>{error}</Alert> : null}

      <Grid container spacing={1.5}>
        <KpiCard label='Total préstamos' value={formatNumber(kpis.totalPrestamos)} />
        <KpiCard label='Monto total prestado' value={formatMoney(kpis.montoTotalPrestado)} />
        <KpiCard label='Saldo pendiente' value={formatMoney(kpis.saldoPendienteTotal)} />
        <KpiCard label='Ticket promedio' value={formatMoney(kpis.ticketPromedio)} />
        <KpiCard label='Plazo promedio (semanas)' value={formatNumber(kpis.plazoPromedioSemanas)} />
        <KpiCard label='Intereses' value={formatMoney(kpis.ingresosIntereses)} />
        <KpiCard label='Tasa aprobación' value={`${formatNumber(kpis?.tasaAprobacion?.porcentaje)}%`} />
        <KpiCard label='Morosidad vencida' value={formatNumber(kpis?.morosidad?.vencidas)} />
      </Grid>

      <Grid container spacing={1.5}>
        <Grid item xs={12} lg={6}>
          <Card>
            <CardContent>
              <Typography variant='h6'>Top clientes</Typography>
              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell>Cliente</TableCell>
                    <TableCell>Monto</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {topClientes.map(item => (
                    <TableRow key={item.cliente_id}>
                      <TableCell>{item.nombre_completo || item.cliente_id}</TableCell>
                      <TableCell>{formatMoney(item.monto_total)}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} lg={6}>
          <Card>
            <CardContent>
              <Typography variant='h6'>Cuotas</Typography>
              <Stack spacing={1} mt={1}>
                <Typography>Pagadas: {formatNumber(cuotasResumen.pagadas)}</Typography>
                <Typography>Pendientes: {formatNumber(cuotasResumen.pendientes)}</Typography>
                <Typography>Parciales: {formatNumber(cuotasResumen.parciales)}</Typography>
                <Typography>Monto vencido: {formatMoney(kpis?.morosidad?.monto_vencido)}</Typography>
              </Stack>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      <Card>
        <CardContent>
          <Typography variant='h6'>Préstamos por mes</Typography>
          <Table size='small'>
            <TableHead>
              <TableRow>
                <TableCell>Mes</TableCell>
                <TableCell>Cantidad</TableCell>
                <TableCell>Monto</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {series.map(item => (
                <TableRow key={item.mes}>
                  <TableCell>{String(item.mes || '').slice(0, 10)}</TableCell>
                  <TableCell>{formatNumber(item.total)}</TableCell>
                  <TableCell>{formatMoney(item.monto)}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </Stack>
  )
}
